:root {
  --color1: #b8c5d5;
  --color2: tomato;
  --color3: #b6ffff;
  --color4: #b6ffff;
  --color5: #493d34;
  --color6: #0a0a0a;
  --color7: #dac2cf;
  --color8: #ecffbd;
  --color9: #9fb2e8;
  --color10: #e8f5c8;
  --grey: #b8c5d5;
  --dark-grey: #60676f;
  --white: #fff;
}

body {
  background: radial-gradient(circle at 20% 30%, var(--color1), transparent 50%), radial-gradient(circle at 50% 50%, var(--color2), transparent 50%), radial-gradient(circle at 70% 70%, var(--color3), transparent 50%), #dcded133;
  background-blend-mode: screen;
  height: 100%;
  min-height: 100vh;
  color: var(--color6);
  background-repeat: no-repeat;
  flex-direction: column;
  margin: 0;
  font-family: Satoshi, Arial, sans-serif;
  font-size: 24px;
  font-weight: normal;
  display: flex;
}

main {
  flex: 1;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
  line-height: 1.5;
}

a {
  color: inherit;
  text-decoration: none;
}

.text-center {
  text-align: center;
}

.display-text-1 {
  letter-spacing: 12px;
  font-family: Satoshi;
  font-size: 5.5vw;
  display: block;
}

.scroll-down-btn-wrapper {
  background-color: #0000;
  border-width: 0;
  border-radius: 0;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 150px;
  display: flex;
  position: absolute;
  bottom: 80px;
  right: 19%;
}

.scroll-down-btn-wrapper .scroll-down-circle {
  text-align: center;
  background: url("case-study-text.6b4d3e3e.png") 0 0 / contain no-repeat;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  animation-name: rotate;
  animation-duration: 50s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
}

nav, footer {
  padding: 10px 60px 20px;
}

nav .hamburger, footer .hamburger {
  display: none;
}

nav #logo, footer #logo {
  align-items: center;
  font-size: 30px;
  font-weight: bold;
  display: flex;
}

nav #menus ul, footer #menus ul {
  float: right;
  gap: 2.6em;
  font-size: 22px;
  font-weight: bold;
  list-style-type: none;
  display: flex;
}

nav #menus ul li, footer #menus ul li {
  float: left;
}

main {
  padding: 0 60px;
}

main #hero-wrapper {
  border: 9px solid var(--white);
  border-radius: 22px;
  padding: 100px 0;
  position: relative;
}

main #hero-wrapper .display-text-1 {
  letter-spacing: .2em;
  color: var(--color5);
  font-weight: 500;
}

main #hero-wrapper #intro {
  text-align: center;
  padding: 0 15vw;
}

main #hero-wrapper #intro #about-me-btn {
  border: 1px solid var(--color6);
  background-color: var(--color7);
  color: var(--color6);
  border-radius: 50px;
  margin-top: 80px;
  padding: 22px 60px;
  font-size: .75em;
  font-weight: bold;
}

@keyframes rotate {
  from {
    transform: rotate(-360deg);
  }

  to {
    transform: rotate(360deg);
  }
}

#list-of-work .wrapper {
  border: 4px solid var(--color6);
  border-radius: 20px;
}

footer {
  font-size: .9em;
  font-weight: bold;
}

footer .pure-menu-list li:not(:first-child):before {
  content: "•";
  padding-right: 13px;
}

#contact-wrapper .wrapper {
  backdrop-filter: blur(10px);
  border: none;
}

#contact-wrapper .contact-info .contact-details .contact-item {
  align-items: flex-start;
  gap: 15px;
  display: flex;
}

#contact-wrapper .contact-info .contact-details .contact-item .contact-icon {
  width: 24px;
  height: 24px;
  color: var(--color6);
  flex-shrink: 0;
  margin-top: 2px;
}

#contact-wrapper .contact-info .contact-details .contact-item .contact-text {
  font-size: .9em;
  line-height: 1.4;
}

#contact-wrapper .contact-info #view-resume-btn {
  border: 1px solid var(--color6);
  background-color: var(--color10);
  color: var(--color6);
  letter-spacing: .4em;
  cursor: pointer;
  border-radius: 15px;
  padding: 22px 60px;
  font-size: .75em;
  font-weight: bold;
  transition: all .3s;
}

#contact-wrapper .contact-info #view-resume-btn:hover {
  background-color: var(--color6);
  color: var(--white);
}

#contact-wrapper .contact-form .form-group {
  margin-bottom: 1.5rem;
}

#contact-wrapper .contact-form .form-group input, #contact-wrapper .contact-form .form-group textarea {
  border: none;
  border-bottom: 2px solid var(--color6);
  width: 100%;
  color: var(--color6);
  background: none;
  outline: none;
  padding: 15px 0;
  font-family: Satoshi, Arial, sans-serif;
  font-size: 1em;
  transition: border-color .3s;
}

#contact-wrapper .contact-form .form-group input::placeholder, #contact-wrapper .contact-form .form-group textarea::placeholder {
  color: var(--dark-grey);
  opacity: .7;
  font-weight: bold;
}

#contact-wrapper .contact-form .form-group input:focus, #contact-wrapper .contact-form .form-group textarea:focus {
  border-bottom-color: var(--color9);
  border-width: 2px;
}

#contact-wrapper .contact-form .form-group textarea {
  resize: vertical;
  min-height: 120px;
  font-family: Satoshi, Arial, sans-serif;
}

#contact-wrapper .contact-form #submit-btn {
  border: 1px solid var(--color6);
  background-color: var(--color10);
  color: var(--color6);
  letter-spacing: .4em;
  cursor: pointer;
  border-radius: 15px;
  width: 100%;
  padding: 22px 60px;
  font-size: .75em;
  font-weight: bold;
  transition: all .3s;
}

#contact-wrapper .contact-form #submit-btn:hover {
  background-color: var(--color6);
  color: var(--white);
}

@media screen and (width <= 768px) {
  .scroll-down-circle {
    display: none;
  }

  nav {
    padding: 1em;
  }

  nav #menus .hamburger {
    float: right;
    flex-direction: column;
    justify-content: space-around;
    height: 25px;
    display: flex;
  }

  nav #menus .hamburger .line {
    background-color: var(--color6);
    width: 30px;
    height: 3px;
  }

  nav #menus ul {
    display: none;
  }

  main {
    padding: .3em 1em;
  }

  main #hero-wrapper {
    padding: 1em .4em;
  }

  main #hero-wrapper #intro {
    margin-top: 1em;
    padding: 0;
  }

  main #hero-wrapper #intro #about-me-btn {
    margin-top: 1em;
  }

  main #contact-wrapper .wrapper {
    border: none;
    margin: 0;
  }

  main #contact-wrapper .contact-info, main #contact-wrapper .contact-form {
    padding: 1em 0;
  }

  main #contact-wrapper .contact-info .contact-details .contact-item {
    gap: 10px;
  }

  main #contact-wrapper .contact-info .contact-details .contact-item .contact-text {
    font-size: .85em;
  }

  main #contact-wrapper .contact-info #view-resume-btn {
    width: 100%;
    padding: 18px 40px;
    font-size: .7em;
  }

  main #contact-wrapper .contact-form #submit-btn {
    padding: 18px 40px;
    font-size: .7em;
  }
}
